import React from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

const NotifyList = (props) => {

    return (
        <>
            <div className='notifyArea'>
            <TransitionGroup className="">
                {props.notifyList.map((item)=>
                    <CSSTransition
                        key={item.id}
                        timeout={2000}
                        classNames="notify"
                        mountOnEnter
                        unmountOnExit
                    >
                    <div className={`notifyItem ${item.class}`}>
                        <div className='notifyText' dangerouslySetInnerHTML={{ __html: item.notifyText }} 
                        /><span
                            className='delNotifyBtn'
                            onClick={()=>props.delNotify(item.id)}
                            ><img src='http://127.0.0.1/react/react-cms/_Graphics/delete4_14px.png' alt=''/></span></div>
                    </CSSTransition>
                )}
            </TransitionGroup>
            </div>
        </>
    )
}

export default NotifyList;